import { useState, useEffect, useRef } from "react";
import { QrReader } from "react-qr-reader";

function App() {
  const [qrDataList, setQrDataList] = useState([]);
  const isScanning = useRef(false); // Trạng thái chặn scan
  const qrContainerRef = useRef(null);

  const playBeep = () => {
    const audioCtx = new (window.AudioContext || window.webkitAudioContext)();
    const oscillator = audioCtx.createOscillator();
    oscillator.type = "sine";
    oscillator.frequency.setValueAtTime(800, audioCtx.currentTime);
    oscillator.connect(audioCtx.destination);
    oscillator.start();
    oscillator.stop(audioCtx.currentTime + 0.1);
  };

  const vibrate = () => {
    if (navigator.vibrate) {
      navigator.vibrate(200);
    }
  };

  const handleScan = (result, error) => {
    if (!result || isScanning.current) return; // Nếu đang chặn scan, bỏ qua

    const text = result.text;
    isScanning.current = true; // Chặn scan ngay lập tức

    setTimeout(() => {
      isScanning.current = false; // Mở khóa scan sau 2 giây
    }, 1000);

    playBeep();
    vibrate();

    setQrDataList((prev) => {
      if (prev.length > 0 && prev[prev.length - 1].value === text) {
        return prev.map((item, index) =>
          index === prev.length - 1 ? { ...item, count: item.count + 1 } : item
        );
      } else {
        return [...prev, { value: text, count: 1 }];
      }
    });
  };

  useEffect(() => {
    if (qrContainerRef.current) {
      qrContainerRef.current.scrollTop = qrContainerRef.current.scrollHeight;
    }
  }, [qrDataList]);

  const handleCopy = () => {
    const textToCopy = qrDataList
      .map((item) =>
        item.count > 1 ? `${item.value} x ${item.count}` : item.value
      )
      .join(", ");
    navigator.clipboard.writeText(textToCopy);
    alert("Đã sao chép!");
  };

  const removeItem = (index) => {
    setQrDataList((prev) => prev.filter((_, i) => i !== index));
  };

  return (
    <div style={{ textAlign: "center", padding: "20px" }}>
      <QrReader
        constraints={{ facingMode: "environment" }}
        onResult={handleScan}
        style={{ width: "300px" }}
      />

      <div
        ref={qrContainerRef}
        style={{
          marginTop: "20px",
          padding: "10px",
          height: "20vh",
          width: "100%",
          display: "flex",
          flexDirection: "column",
          overflowY: "auto",
          border: "1px solid black",
          borderRadius: "5px",
          fontSize: "18px",
          textAlign: "left",
        }}
      >
        {qrDataList.length === 0 ? (
          <p style={{ textAlign: "center" }}>Chưa quét được QR nào</p>
        ) : (
          qrDataList.map((item, index) => (
            <div
              key={index}
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                padding: "8px",
                borderBottom: "1px solid #ccc",
                fontWeight: index === qrDataList.length - 1 ? "bold" : "normal",
                color: index === qrDataList.length - 1 ? "green" : "black",
              }}
            >
              <span>
                {item.value}{" "}
                {item.count > 1 && (
                  <span style={{ color: "hotpink", fontWeight: "bold" }}>
                    x {item.count}
                  </span>
                )}
              </span>
              <button
                onClick={() => removeItem(index)}
                style={{
                  backgroundColor: "red",
                  color: "white",
                  border: "none",
                  borderRadius: "5px",
                  padding: "5px 10px",
                  cursor: "pointer",
                }}
              >
                🗑️
              </button>
            </div>
          ))
        )}
      </div>

      <button
        onClick={handleCopy}
        style={{
          marginTop: "15px",
          padding: "10px 20px",
          fontSize: "16px",
          cursor: "pointer",
          borderRadius: "5px",
          border: "none",
          background: "#007bff",
          color: "white",
        }}
      >
        📋 Copy
      </button>
    </div>
  );
}

export default App;
